import S from 'fluent-json-schema'
import {
  ClientEntity,
  Page,
  PageSearchQuery,
  sBCAddress,
  sBigInt,
  sClientEntity,
  sPageSearchQuery,
} from '../core'
import { sWeb3ProtocolContract, Web3ProtocolContract } from '../web3-client'
import { TOKENS_ROUTES_KEY } from './token.const'

/**
 * Client Token interface
 */
export interface Token extends TokenData, ClientEntity {}

export function sToken(isPartial?: boolean) {
  return S.object()
    .id('#token')
    .additionalProperties(false)
    .extend(sClientEntity(isPartial))
    .extend(sTokenData(isPartial))
}

export interface TokenData {
  name: string
  address: string
  color?: string

  // ETH, etc
  symbol: string
  decimals: number

  chainId: string

  // Oracle
  oracle?: TokenOracle
}

export function sTokenData(isPartial?: boolean) {
  return S.object()
    .additionalProperties(false)
    .prop('name', S.string())
    .description('The name of the token.')
    .prop('chainId', S.string())
    .description('The chainID of the token.')
    .prop('address', sBCAddress())
    .description('The blockchain address of the token.')
    .prop('symbol', S.string())
    .prop('color', S.string())
    .prop('decimals', S.number())
    .prop('oracle', sTokenOracle())
    .description('Token oracle')
    .required(
      isPartial ? [] : ['name', 'chainId', 'address', 'symbol', 'decimals']
    )
}

export interface TokenOracle extends Web3ProtocolContract {
  USDCAddress?: string
  wETHAddress?: string
  stETHAddress?: string
  USDEAddress?: string
  MATICAddress?: string
  ARBAddress?: string
  OPAddress?: string
  fee?: number
}

export function sTokenOracle() {
  return S.object()
    .additionalProperties(false)
    .prop('USDCAddress', sBCAddress())
    .prop('wETHAddress', sBCAddress())
    .prop('stETHAddress', sBCAddress())
    .prop('USDEAddress', sBCAddress())
    .prop('ARBAddress', sBCAddress())
    .prop('OPAddress', sBCAddress())
    .prop('fee', S.number())
    .extend(sWeb3ProtocolContract())
}

export interface TokenPriceData {
  price: string
  decimals: number
}

export function sTokenPriceData() {
  return S.object()
    .additionalProperties(false)
    .prop('price', sBigInt())
    .required()
    .prop('decimals', S.number())
    .required()
}

export enum TokenErrorCodes {
  notDeletable = 'TOKEN_NOT_DELETABLE',
}

export type TokenFields =
  | '_id'
  | 'name'
  | 'chainId'
  | 'address'
  | 'symbol'
  | 'decimals'
  | 'oracle'

export const TOKEN_FIELDS = [
  '_id',
  'name',
  'chainId',
  'address',
  'symbol',
  'decimals',
  'oracle',
  'createdAt',
  'createdBy',
  'updatedAt',
  'updatedBy',
]

export const TOKEN_SORT_FIELDS = ['_id', 'name']

export interface TokensSearchQuery extends PageSearchQuery<'name'> {
  name?: string
  'name:ct'?: string
  address?: string
  symbol?: string
  chainId?: string
}

export function sTokensSearchQuery() {
  return S.object()
    .additionalProperties(false)
    .prop('name', S.string())
    .description('Name of the token that must match.')
    .prop('name:ct', S.string())
    .description('Name of the token that must be contained.')
    .prop('address', sBCAddress())
    .description('Blockchain address of the token that must match.')
    .prop('symbol', S.string())
    .description('Symbol of the token that must match.')
    .prop('chainId', S.string())
    .description('ChainID of the token that must match.')
    .extend(sPageSearchQuery(TOKEN_FIELDS, TOKEN_SORT_FIELDS))
}

export interface TokensClient {
  search: (params?: TokensSearchQuery) => Promise<Page<Token>>
  list: (params?: TokensSearchQuery) => Promise<Token[]>
  findOne: (params?: TokensSearchQuery) => Promise<Token | undefined>
  readOne: (params: TokensSearchQuery) => Promise<Token>
  create: (body: TokenData) => Promise<Token>
}

export enum TokenRoutes {
  v1Create = `v1/${TOKENS_ROUTES_KEY}`,
  v1Delete = `v1/${TOKENS_ROUTES_KEY}/:tokenId`,
  v1Read = `v1/${TOKENS_ROUTES_KEY}/:tokenId`,
  v1Update = `v1/${TOKENS_ROUTES_KEY}/:tokenId`,
  v1Search = `v1/${TOKENS_ROUTES_KEY}`,
}
